/* purgecss start ignore */

.MuiSlider-root {
  margin: 12px 0;
}

.MuiSlider-root .MuiSlider-thumb {
    border-width: 4px;
    border-style: solid;
    --tw-border-opacity: 1;
    border-color: rgba(var(--lc-color-primary), var(--tw-border-opacity));
    width: 16px;
    height: 16px;
    background-color: #fff;
  }

.MuiSlider-root .MuiSlider-rail, .MuiSlider-root .MuiSlider-track {
    --tw-bg-opacity: 1;
    background-color: rgba(var(--lc-color-primary), var(--tw-bg-opacity));
  }

.MuiSlider-root .MuiSlider-rail,
  .MuiSlider-root .MuiSlider-track {
    height: 6px;
  }

.MuiSlider-root .MuiSlider-rail {
    opacity: 1;
    background-color: #f3f3f3;
    border-radius: 3px;
  }

.MuiSlider-root .MuiSlider-valueLabel {
    background: none;
    font-size: 12px;
    left: 50%;
    top: -26px;
    transform: translate(-50%, 0) !important;
  }

.MuiSlider-root .MuiSlider-valueLabel > span {
      display: block;
      width: auto;
      height: auto;
      background-color: #f3f3f3;
      padding: 2px 6px;
      border-radius: 4px;
      width: auto;
      transform: none;
      white-space: nowrap;
    }

.MuiSlider-root .MuiSlider-valueLabel > span > span {
        --tw-text-opacity: 1;
        color: rgba(var(--lc-color-gray-darker), var(--tw-text-opacity));
        display: block;
        transform: none;
        text-align: center;
      }

/* purgecss end ignore */
